@media (max-width: 768px) {
  .TodoSearch {
    display: block;
    margin: 20px auto;
    height: 40px;
    width: 90%;
    text-align: center;
    border-radius: 20px;
    border: 2px solid black;
    background-image: url("./search-icon.svg");
    background-repeat: no-repeat;
    background-size: 25px 25px;
    background-position: 1rem center;
  }

  .TodoSearch:focus {
    outline-color: black;
  }
}
